//* Checklist
.task-details-main-checklist {
  padding: 0;
  display: grid;
  grid-auto-columns: 56px 424px 88px;
  grid-template-areas:
    'icon title remove-btn'
    'percentage advancement advancement'
    'todos todos todos'
    '. add-btn .';
  margin-bottom: 24px;
  gap: 12px 0px; //TODO: Space between is not yet pixel perfect

  &-icon {
    grid-area: icon;
    justify-self: flex-start;
    margin-left: 22px;
    color: #172b4d;
    font-size: 20px;
  }

  &-title {
    grid-area: title;
    margin: 0;
    justify-self: flex-start;
    font-size: 16px;
    font-weight: 600;
    color: #172b4d;

    &-input {
      grid-area: title;
    }
  }

  &-remove-btn {
    grid-area: remove-btn;
    justify-self: flex-end;
    @include btn('task-edit',
      $list-btn-bg-clr-modal-hover,
      6px 12px,
      $task-details-btn-bg-clr-hover );
    display: flex;
    margin: 0;
    width: fit-content;
    align-self: center;
  
  }

  &-progress-bar {
    &-percentage {
      grid-area: percentage;
      font-size: 11px;
      justify-self: flex-start;
      margin-left: 22px;
      color: #5e6c84;
    }

    &-advancement {
      grid-area: advancement;
      align-self: center;
      background: #091e4214;
      border-radius: 4px;
      clear: both;
      height: 8px;
      // margin: 0 0 0 40px;
      overflow: hidden;
      position: relative;

      &-transition {
        transition: background 0.14s, width 0.2s;
        background: rgb(69, 185, 178);
        color: #111;

        & span {
          // z-index: 3;
          color: #5e6c84;
          font-size: 11px;
          left: 0;
          line-height: 10px;
          position: absolute;
          text-align: center;
          top: -1px;
          width: 32px;
        }
      }
    }
  }

  &-todos-list {
    grid-area: todos;
    list-style: none;
    padding: 0;
    display: flex;
    flex-direction: column;
    // gap: 10px 0px;

    &-todo {
      display: grid;
      grid-auto-columns: 56px 424px 88px;
      grid-template-areas: 'checkbox title btn-container';
      align-items: center;
      padding: 10px 10px 10px 0;

      border-radius: 4px;
      clear: both;
      position: relative;
      transform-origin: left bottom;
      transition-duration: .3s;
      transition-property: transform, opacity, height, padding, margin, background-color;
      transition-timing-function: ease-in;

      &:hover {
        background-color: #f4f5f7;

          & button {
            padding: 10px;
            opacity: 1;
          }
        
      }

      &-checkbox {
        grid-area: checkbox;
        margin: 0;
        margin-left: 22px;
        min-height: 16px;
        min-width: 16px;
        max-height: 16px;
        max-width: 16px;
      }

      &-title {
        grid-area: title;
        padding: 0;
        display: grid;
        justify-items: flex-start;

        & p {
          margin: 0;
        }
      }

      &-btn-container {
        grid-area: btn-container;
        display: flex;
        flex-direction: row-reverse;

        &-remove-btn {
          @include btn('task-edit',
            $list-btn-bg-clr-modal-hover,
            6px 12px,
            $task-details-btn-bg-clr-hover );
          display: flex;
          margin: 0;
          // align-self: center;
          place-content: center;
          height: 24px;
          width: 24px;
          opacity: 0;
        }
      }
    }
  }

  &-add-todo {
    &-btn {
      grid-area: add-btn;
      justify-self: flex-start;
      @include btn('task-edit',
        $list-btn-bg-clr-modal-hover,
        6px 12px,
        $task-details-btn-bg-clr-hover );
      display: flex;
      margin: 0;
      width: fit-content;
    }

    &-input {
      grid-area: add-btn;
    }
  }
}

.task-details-main-checklist {
  h3 {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI',
      Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue',
      sans-serif;
    font-size: 16px;
    line-height: 20px;
    display: inline-block;
    margin: 0;
    min-height: 18px;
    min-width: 40px;
    width: auto;
    // margin: 6px 0;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
    color: #172b4d;

    // padding: 0 100px 0 0;
    text-align: left;


  }
}

