.todo-list-preview {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.todo-list-preview {
  border-radius: 4px;
  clear: both;
  // padding-left: 40px;
  position: relative;
  transform-origin: left bottom;
  transition-duration: 0.3s;
  transition-property: transform, opacity, height, padding, margin,
    background-color;
  transition-timing-function: ease-in;

  p {
    align-self: center;
    flex: 1;
    margin-bottom: 0;
    min-height: 20px;
    cursor: pointer;
    color: #172b4d;
  }

  .delete {
    background-color: #091e420a;
    color: #172b4d;
    opacity: 0;
  }

  &:hover {
    background-color: #f4f5f7;

    .delete {
      opacity: 1;
    }
  }
}

