form {
  margin: 0;
  display: flex;
  flex-direction: column;

  textarea {
    -webkit-appearance: none;
    background-color: #fafbfc;
    border: none;
    border-radius: 3px;
    box-shadow: inset 0 0 0 2px #dfe1e6;
    box-sizing: border-box;
    display: block;
    line-height: 20px;
    outline: none;
    padding: 8px 12px;
    transition-duration: 85ms;
    transition-property: background-color, border-color, box-shadow;
    transition-timing-function: ease;
    background-color: $clr-white;
    box-shadow: inset 0 0 0 2px #0079bf;
    color: var(--ds-text, #172b4d);
    cursor: text;
    float: none;
    resize: vertical;
    // width: 100%;
    width: 512px;
    overflow: hidden;
    overflow-wrap: break-word;
    margin: 0;
    margin-bottom: 12px;
  }
}

.todo-btns {
  margin: 0;
  display: flex;
}

.cancel-btn {
  align-items: center;
  background-color: #091e420a;
  border: none;
  border-radius: 3px;
  box-shadow: none;
  box-sizing: border-box;
  cursor: pointer;
  display: inline-flex;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans,
    Ubuntu, Droid Sans, Helvetica Neue, sans-serif;
  font-size: 14px;
  font-weight: 400;
  justify-content: center;
  line-height: 20px;
  padding: 6px 12px;
  text-decoration: none;
  transition-duration: 85ms;
  transition-property: background-color, border-color, box-shadow;
  transition-timing-function: ease;
  white-space: normal;
  background-color: initial;
  border: none;
  box-shadow: none;
  margin: 0;
  color: #172b4d;

  &:hover {
    background-color: #f4f5f7;
  }
}

.todo-btn {
  align-items: center;
  background-color: var(--ds-background-neutral, #091e420a);
  border: none;
  border-radius: 3px;
  box-shadow: none;
  box-sizing: border-box;
  color: var(--ds-text, #172b4d);
  color: var(--ds-text, inherit);
  cursor: pointer;
  display: inline-flex;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans,
    Ubuntu, Droid Sans, Helvetica Neue, sans-serif;
  font-size: 14px;
  font-weight: 400;
  justify-content: center;
  line-height: 20px;
  padding: 6px 12px;
  text-decoration: none;
  transition-duration: 85ms;
  transition-property: background-color, border-color, box-shadow;
  transition-timing-function: ease;
  white-space: normal;
  background-color:  #0079bf;
  border: none;
  box-shadow: none;
  color: $clr-white;
  margin: 0;
}

.todo-btns-btn {
  align-items: center;
  background-color: #091e420a;
  border: none;
  border-radius: 3px;
  box-shadow: none;
  box-sizing: border-box;
  cursor: pointer;
  display: inline-flex;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Noto Sans,
    Ubuntu, Droid Sans, Helvetica Neue, sans-serif;
  font-size: 14px;
  font-weight: 400;
  justify-content: center;
  line-height: 20px;
  padding: 6px 12px;
  text-decoration: none;
  transition-duration: 85ms;
  transition-property: background-color, border-color, box-shadow;
  transition-timing-function: ease;
  white-space: normal;
  color: #172b4d;
}

.add-todo-btn {
  display: block;
}

